body {
  margin: 0;
  font-family: "Lato";
  font-size: 16px;
  background: #F5F5F5;
}

.container {
  padding: 0 2rem;
}

header {
  background: linear-gradient(-45deg, rgba(27, 158, 208, 0.8), rgba(22, 210, 139, 0.8)), url("../bg.jpeg");
  background-size: cover;
  padding: 3rem 1rem;
  color: white;
  clip-path: polygon(0 0, 100% 0, 100% 100%, 0 calc(100% - 5vw));
}

header img {
  cursor: pointer;
}

header h2 {
  width: 100%;
  font-size: 2rem;
  font-weight: 600;
  margin: 2rem 0;
}

header .nav-services {
  display: none;
  justify-content: center;
}

header .nav-services span {
  padding: 0 0.5rem;
  color: rgba(255, 255, 255, 0.8);
  -webkit-transition: all 0.25s ease-in-out;
  -moz-transition: all 0.25s ease-in-out;
  -ms-transition: all 0.25s ease-in-out;
  -o-transition: all 0.25s ease-in-out;
  transition: all 0.25s ease-in-out;
  cursor: pointer;
}

header .nav-services span:hover {
  color: rgba(255, 255, 255, 0.9);
}

.section {
  background: #F5F5F5;
}

.section .container {
  padding: 3rem 2rem;
  background-repeat: no-repeat;
  background-position-x: right;
}

section {
  background: white;
  padding: 2rem 0;
}

section#about p {
  font-size: 1.3rem;
  font-weight: 300;
  line-height: 2rem;
  text-align: justify;
  color: #6c665c;
  margin: 0;
}

section#portafolio .container {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  grid-gap: 1rem;
}

section#portafolio .container .item {
  position: relative;
}

section#portafolio .container .item img {
  width: 100%;
  height: 100%;
}

section#portafolio .container .item .cover {
  background: linear-gradient(-45deg, #1B9ED0, #16D28B);
  position: absolute;
  opacity: 0.95;
  top: 0;
  left: 0;
  text-align: center;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-direction: column;
  color: white;
  text-shadow: 0 2px 0 rgba(0, 0, 0, 0.3);
  text-decoration: none;
  -webkit-transition: all 0.25s ease-in-out;
  -moz-transition: all 0.25s ease-in-out;
  -ms-transition: all 0.25s ease-in-out;
  -o-transition: all 0.25s ease-in-out;
  transition: all 0.25s ease-in-out;
}

section#portafolio .container .item .cover:hover {
  text-shadow: none;
  opacity: 0.7;
  cursor: pointer;
}

section#portafolio .container .item .cover span {
  letter-spacing: 1px;
  font-weight: 600;
}

section#servicios {
  background-image: url("../pattern.svg");
  background-size: 6rem;
  background-attachment: fixed;
}

footer {
  background: url("../footer.jpeg");
  background-size: cover;
}

footer .cover {
  padding: 2rem 0;
  background: rgba(0, 0, 0, 0.05);
  height: 100%;
  position: relative;
}

.btn {
  border: 1px solid white;
  border-radius: 4px;
  padding: 1rem 2rem;
  cursor: pointer;
  text-decoration: none;
  font-weight: 600;
  letter-spacing: 1px;
  -webkit-transition: all 0.25s ease-in-out;
  -moz-transition: all 0.25s ease-in-out;
  -ms-transition: all 0.25s ease-in-out;
  -o-transition: all 0.25s ease-in-out;
  transition: all 0.25s ease-in-out;
}

.btn.btn-block {
  display: block;
  width: 100%;
}

.btn.btn-small {
  padding: 0.5rem 1rem;
  font-weight: 400;
}

.btn.btn-white {
  background-color: #fff;
  border-color: #fff;
  color: white;
  outline: none !important;
}

.btn.btn-white:hover {
  background-color: #e6e6e6;
  border-color: #e6e6e6;
}

.btn.btn-white:active {
  background-color: #cccccc;
  border-color: #cccccc;
}

.btn.btn-white.btn-outline {
  background-color: transparent;
  color: #fff;
}

.btn.btn-white.btn-outline:hover {
  background: #fff;
  color: #555 !important;
}

.btn.btn-white.btn-outline:active {
  opacity: 0.9;
}

.btn.btn-primary {
  background-color: #1B9ED0;
  border-color: #1B9ED0;
  color: white;
  outline: none !important;
}

.btn.btn-primary:hover {
  background-color: #157ca3;
  border-color: #157ca3;
}

.btn.btn-primary:active {
  background-color: #0f5976;
  border-color: #0f5976;
}

.btn.btn-primary.btn-outline {
  background-color: transparent;
  color: #1B9ED0;
}

.btn.btn-primary.btn-outline:hover {
  background: #1B9ED0;
  color: #555 !important;
}

.btn.btn-primary.btn-outline:active {
  opacity: 0.9;
}

.btn.btn-secondary {
  background-color: #16D28B;
  border-color: #16D28B;
  color: white;
  outline: none !important;
}

.btn.btn-secondary:hover {
  background-color: #11a46c;
  border-color: #11a46c;
}

.btn.btn-secondary:active {
  background-color: #0c764e;
  border-color: #0c764e;
}

.btn.btn-secondary.btn-outline {
  background-color: transparent;
  color: #16D28B;
}

.btn.btn-secondary.btn-outline:hover {
  background: #16D28B;
  color: #555 !important;
}

.btn.btn-secondary.btn-outline:active {
  opacity: 0.9;
}

.nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.nav__logo {
  height: 2rem;
}

.nav__right {
  display: none;
}

.nav__element {
  color: rgba(255, 255, 255, 0.9);
  text-decoration: none;
  margin: 0 1rem;
  position: relative;
  font-weight: 600;
  cursor: pointer;
  -webkit-transition: all 0.25s ease-in-out;
  -moz-transition: all 0.25s ease-in-out;
  -ms-transition: all 0.25s ease-in-out;
  -o-transition: all 0.25s ease-in-out;
  transition: all 0.25s ease-in-out;
}

.nav__element:hover {
  color: white;
}

.nav__element:hover::after {
  background: rgba(255, 255, 255, 0.6);
  width: 70%;
}

.nav__element::after {
  transition: all 0.25s ease-in-out;
  content: "";
  position: absolute;
  display: block;
  height: 2px;
  margin: 0 auto;
  left: 0;
  right: 0;
  bottom: -0.5rem;
  width: 100%;
}

.form__controller {
  background: white;
  padding: 1rem;
  margin: 0 0 1rem 0;
  border-radius: 4px;
  border: none;
  outline: none;
  width: 100%;
  opacity: 0.8;
  resize: none;
  -webkit-transition: all 0.25s ease-in-out;
  -moz-transition: all 0.25s ease-in-out;
  -ms-transition: all 0.25s ease-in-out;
  -o-transition: all 0.25s ease-in-out;
  transition: all 0.25s ease-in-out;
}

.form__controller:active, .form__controller:focus {
  opacity: 1;
}

.pricing .plan {
  box-shadow: 0 0 27px rgba(0, 0, 0, 0.2);
  border-radius: 1rem;
  padding: 2rem;
  text-align: center;
  position: relative;
  background: white;
  transform: scale(1);
  -webkit-transition: all 0.25s ease-in-out;
  -moz-transition: all 0.25s ease-in-out;
  -ms-transition: all 0.25s ease-in-out;
  -o-transition: all 0.25s ease-in-out;
  transition: all 0.25s ease-in-out;
}

.pricing .plan img {
  display: none;
  width: 60%;
}

.pricing .plan h3 {
  margin-top: 0;
}

.pricing .plan.best {
  margin: 2rem 0;
}

.pricing .plan ul {
  font-size: 0.8rem;
  padding-left: 1.5rem;
}

.pricing .plan ul li {
  position: relative;
  list-style: none;
  text-align: left;
  padding: 0.4rem 0;
  color: #6C757D;
}

.pricing .plan ul li:after {
  content: '\2713';
  color: #1B9ED0;
  background-color: transparent;
  position: absolute;
  left: -1.5rem;
  top: 0.48em;
  font-weight: 600;
}

.section__title {
  color: #1B9ED0;
  margin: 0 0 1rem 0;
  font-size: 2rem;
}

.section__subtitle {
  color: #6C757D;
  font-weight: 400;
  margin: 0;
}

@media (min-width: 480px) {
  header .nav-services {
    display: flex;
  }
  .section .container {
    background: none !important;
  }
  section {
    padding: 2rem 0;
  }
  footer .cover {
    padding: 4rem 0;
  }
}

@media (min-width: 600px) {
  header {
    padding: 5rem 0;
  }
  header h2 {
    width: 50%;
    font-size: 3rem;
    padding: 5rem 0;
  }
  .container {
    width: 80vw;
    margin: 0 auto;
  }
  .section .container {
    padding: 4rem 0;
    background-position-y: 50% !important;
    background-size: 15rem;
  }
  .section:first-of-type .container {
    padding-top: 3rem;
  }
  .section.about .container {
    background-image: url("../sections/quienes-somos.svg");
    background-size: 15rem;
  }
  .section.portafolio .container {
    background-image: url("../sections/responsive.svg");
    background-size: 21rem;
  }
  .section.contacto .container {
    background-image: url("../sections/contacto.svg");
    background-size: 15rem;
  }
  section {
    padding: 6rem 0;
  }
  section#about p {
    line-height: 3rem;
  }
  section#portafolio .container {
    grid-template-columns: repeat(2, 1fr);
  }
  footer .cover {
    padding: 6rem 0;
  }
  .nav__right {
    display: block;
  }
  .pricing {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .pricing .plan {
    width: 33%;
    padding: 3rem 2rem 2rem 2rem;
  }
  .pricing .plan img {
    display: inline;
  }
  .pricing .plan:hover {
    cursor: pointer;
    transform: scale(1.05);
  }
  .pricing .plan.best {
    width: 34%;
    z-index: 2;
  }
  .pricing .plan:first-of-type {
    border-radius: 1rem 0 0 1rem;
  }
  .pricing .plan:last-of-type {
    border-radius: 0 1rem 1rem 0;
  }
  .form__grid {
    display: flex;
    justify-content: space-around;
  }
  .form__controller:first-child {
    margin-right: 0.5rem;
  }
  .form__controller:nth-child(2) {
    margin: 0 0.5rem 1rem 0.5rem 0;
  }
  .form__controller:last-child {
    margin: 0 0 1rem 0.5rem;
  }
}

@media (min-width: 1000px) {
  .container {
    width: 70vw;
  }
  section#portafolio .container {
    grid-template-columns: repeat(3, 1fr);
  }
}
