.form{
  &__controller{
    background: white;
    padding: 1rem;
    margin: 0 0 1rem 0;
    border-radius: 4px;
    border: none;
    outline: none;
    width: 100%;
    opacity: 0.8;
    resize: none;
    @include transition();
    &:active, &:focus{
      opacity: 1;
    }
  }
}

@mixin form--md{
  .form{
    &__grid{
      display: flex;
      justify-content: space-around;
    }
    &__controller{
      &:first-child{
        margin-right: 0.5rem;
      }
      &:nth-child(2){
        margin: 0 0.5rem 1rem 0.5rem 0;
      }
      &:last-child{
        margin: 0 0 1rem 0.5rem;
      }
    }
  }
}